*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

#root{
	display:flex;
	flex-direction: column;
	align-items: center;
}

.App{
	width: 800px;  
}

.post{
	display: flex;
	padding: 15px;
	border: 2px solid teal;
	margin-top: 15px;
	justify-content: space-between;
	align-items: center;
}
.post__btns{
	display: flex;
}
.paggination{
	margin-top: 30px;
	text-align: center;
}
.paggination__item{
	padding: 8px;	
	border: 1px solid teal;
	cursor: pointer;
	margin: 0 2px;
}

.paggination__current{
	background: teal;
	color: #fff;
}

.item-enter {
  opacity: 0;
}
.item-enter-active {
  opacity: 1;
  transition: opacity 500ms;
}
.item-exit {
  opacity: 1;
}
.item-exit-active {
  opacity: 0;
  transition: opacity 500ms;
}

.navbar{
	height: 40px;
	width: 100%;
	display: flex;
	align-items: center;
	padding: 0 15px;
	background: lightgray;

}
.navbar__links{
	margin-left: auto;

}
.navbar__links a{
	margin: 0 20px;
	text-decoration: none;
	font-size: 18px;
	color: black;
	transition: color .3s;
}
.navbar__links a:hover{
	color: red;
}